import './style/main.css'
import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import * as dat from 'dat.gui'




/**
 * Sizes
 */
const sizes = {}
sizes.width = window.innerWidth
sizes.height = window.innerHeight

window.addEventListener('resize', () =>
{
    // Save sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
})

/**
 * Environnements
 */
// Scene
const scene = new THREE.Scene()

// Camera
const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height, 0.1, 100)
camera.position.z = 8
camera.position.y = 2
scene.add(camera)

//Debug
const gui = new dat.GUI({
    width: 400
});
const parameters = {}
parameters.count = 100000;
parameters.size = 0.01;
parameters.radius = 4;
parameters.branch = 3;
parameters.spin = 1;
parameters.randomnessPower = 4;
parameters.insideColor = '#8c3be3'
parameters.outsideColor = '#001763'
parameters.sizePerspective = false;


let galaxyGeometry = null
let galaxyMaterial = null
let galaxy = null

//Galaxy Generator
const generateGalaxy = () =>{

    if(galaxy !== null){

        galaxyMaterial.dispose();
        galaxyGeometry.dispose();
        scene.remove(galaxy);
    }
  
    galaxyGeometry = new THREE.BufferGeometry();
    const positions = new Float32Array(parameters.count * 3);
    const colors = new Float32Array(parameters.count * 3);
    const colorInside = new THREE.Color(parameters.insideColor);
    const colorOutside = new THREE.Color(parameters.outsideColor);


    for(let i = 0; i < parameters.count; i++)
    {
        //Positions
        const i3 = i * 3;
        const radius = Math.random() * parameters.radius;
        const spinAgle = radius * parameters.spin;
        //To make the value from 0 to 1. deviede value by 3
        const branchAngle = (i % parameters.branch)/ parameters.branch * Math.PI*2;

        const randomX = Math.pow(Math.random(), parameters.randomnessPower) * (Math.random() < 0.5 ? 1 : -1)
        const randomY = Math.pow(Math.random(), parameters.randomnessPower) * (Math.random() < 0.5 ? 1 : -1)
        const randomZ = Math.pow(Math.random(), parameters.randomnessPower) * (Math.random() < 0.5 ? 1 : -1)


        positions[i3 + 0] = Math.cos(branchAngle + spinAgle) * radius + randomX
        positions[i3 + 1] = 0 + randomY
        positions[i3 + 2] = Math.sin(branchAngle + spinAgle) * radius + randomZ

        if(i < 10){
            console.log(radius)
        }
        const mixedColor = colorInside.clone()
        mixedColor.lerp(colorOutside, radius / parameters.radius)

        //console.log(mixedColor)
        //Colors
        colors[i3 + 0] = mixedColor.r
        colors[i3 + 1] = mixedColor.g
        colors[i3 + 2] = mixedColor.b

    }
    galaxyGeometry.setAttribute('position', new THREE.BufferAttribute(positions, 3));
    galaxyGeometry.setAttribute('color', new THREE.BufferAttribute(colors, 3));
    
    

    galaxyMaterial = new THREE.PointsMaterial({
        size: parameters.size,
        sizeAttenuation: parameters.sizePerspective,
        vertexColors: true
    });

    galaxy = new THREE.Points(galaxyGeometry, galaxyMaterial); 
    scene.add(galaxy);
}
generateGalaxy();

//Controls
const controls = new OrbitControls(camera, document.querySelector('.webgl'));
controls.enableDamping = true;
controls.update();



gui.add(parameters, 'count').min(0).max(100000).step(10).onFinishChange(generateGalaxy);
gui.add(parameters, 'size').min(0.001).max(0.1).step(0.001).onFinishChange(generateGalaxy);
gui.add(parameters, 'radius').min(0.1).max(5).step(0.1).onFinishChange(generateGalaxy);
gui.add(parameters, 'branch').min(2).max(6).step(1).onFinishChange(generateGalaxy);
gui.add(parameters, 'spin').min(-1).max(1).step(0.1).onFinishChange(generateGalaxy);
gui.add(parameters, 'randomnessPower').min(1).max(5).step(1).onFinishChange(generateGalaxy);
gui.addColor(parameters, 'insideColor').onFinishChange(generateGalaxy);
gui.addColor(parameters, 'outsideColor').onFinishChange(generateGalaxy);
gui.add(parameters, 'sizePerspective').onFinishChange(generateGalaxy);


// Renderer
const renderer = new THREE.WebGLRenderer({
    canvas: document.querySelector('.webgl')
})
renderer.setPixelRatio(window.devicePixelRatio)
renderer.setSize(sizes.width, sizes.height)

/**
 * Loop
 */
const clock = new THREE.Clock();

const loop = () =>
{

    const elapsedTime = clock.getElapsedTime();

    // Update
    controls.update();
    galaxy.rotation.y = elapsedTime/8;


    // Render
    renderer.render(scene, camera)

    // Keep looping
    window.requestAnimationFrame(loop)
}
loop()